<template>
 <div id="ArticleID" v-if="articleData.title" >
     <h1>{{ articleData.title }}</h1>
     <p class="author-time">
         作者：{{articleData.author.user}}  时间：{{articleData.time | authorTime}}
     </p>
     <div id="article-content" ref="mdToHTML"></div>
 </div>
</template>

<script>
 export default {
    name: "ArticleID",
   data () {
     return {
         articleData: {},
     }
   },
//    过滤器
   filters: {
    authorTime(value){
      let date = new Date(value);
      let YY = date.getFullYear();
      let MM = date.getMonth() + 1;
      let DD = date.getDate();
      let hh = date.getHours();
      let mm = date.getMinutes();
      let ss = date.getSeconds();


      // 双数字处理
      MM = (MM < 10 ? "0" : "") + MM;
      DD = (DD < 10 ? "0" : "") + DD;
      hh = (hh < 10 ? "0" : "") + hh;
      mm = (mm < 10 ? "0" : "") + mm;
      ss = (ss < 10 ? "0" : "") + ss;

      return `${YY}-${MM}-${DD}  ${hh}:${mm}:${ss}`
    }
  },
  watch: {
      "$route": function () {
          this.refresh()
      }
  },
  methods: {
     async refresh(){
         //    获取文章的id,方便向后端查询数据
        let id = this.$route.params.id

        // 向后端查询对应id的文章
        let res = await this.$axios({
            method: "get",
            url: "/art/id",
            params: {id}
        })
        if(res.data.code !== 0){
            return this.$router.push("/NotFind")
        }
        this.articleData = res.data.data

        // if(res.data.code != 0){
        //     return this.$router.push("/NotFind")
        // }
        let {data} = await this.$axios({
               method: "GET",
               url: this.articleData.src
           })

        this.$refs.mdToHTML.innerHTML = ""
        editormd.markdownToHTML("article-content", {
            markdown: data,
            toc: false,
            emoji: true,
            taskList: true,
            tex: true,
            flowChart: true,
            sequenceDiagram: true
    
        })
     }
  },
  created() {
    this.refresh()
  }
   
 }
</script>
<style lang="less" scoped>
#ArticleID{
    user-select: none;
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: #fff;
    font-family: "Quicksand","Microsoft YaHei",sans-serif;
    cursor: pointer;
    h1{
        text-align: center;
    }
    .author-time{
        text-align: center;
        font-size: 12px;
        color: #aaa;
    }
    
}
</style>